@import 'variables';

.polityka-prywatnosci-container {
  display: flex;
  flex-direction: column;
  background-color: $colorWhiteTheme;
  padding-top: 100px;
  line-height: 1.2;
  color: $colorBlackTheme;
  position: relative;
  .top-background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 200px;
    background-color: $colorDarkGrey;
    display: none; }
  h1 {
    flex-basis: 100%;
    padding: 20px; }

  div {
    padding: 20px;
    h3 {
      padding: 20px 0 20px 0; } } }

@media screen and ( min-width: 600px ) {
  .polityka-prywatnosci-container {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    padding-top: 300px;

    div {
      flex-basis: 50%; }
    .top-background {
      display: block; } } }

@media screen and ( min-width: 1024px ) {
  .polityka-prywatnosci-container {
    div {
      flex-basis: 25%;
      padding: 20px; } } }
