@import 'variables';
@import 'Home';
@import 'Kontakt';
// @import 'Cennik'
@import 'Portfolio';
@import 'Onas';
@import 'fancy-button';
@import 'nav';
@import 'Loader';
@import 'Polityka-Prywatnosci';

@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@400;700&display=swap');
html {
  scroll-behavior: smooth; }

* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  box-sizing: border-box;
  color: unset; }

// a:active, a:focus, a:visited, a

.container {
  // font-family: 'Raleway', 'arial'
  // font-family: 'Arial'
  // font-family: 'IBM Plex Mono'
  min-height: 200px;
  .content {
    position: relative;
    top: $navHeight;
    width: 100%;
    sbackground-color: pink;
    .background-img {
      position: relative;
      top: 0;
      left: 0;
      max-height: 80vh;
      overflow: hidden;
      img {
        width: 100%;
        filter: grayscale(70%) brightness(0.3); }
      .text-on-image {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 25px;
        text-align: center;
        color: white;
        width: 80%; } } }

  .footer {
    background-color: #121111;
    color: $colorDarkGrey;
    min-height: 200px;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    position: relative;
    top: $navHeight;

    p {
      margin: 5px 0 5px 0; }
    .fb {
      background-color: #222;
      color: $colorDarkGrey;
      width: 60px;
      height: 60px;
      padding: 5px;
      display: flex;
      justify-content: center;
      align-items: center; }
    .fb:hover {
      cursor: pointer;
      color: white;
      background-color: #3b5998; } } }

@media ( min-width: 600px ) {
  .container {
    .content {
      top: 0;
      .background-img {
        .text-on-image {
          top: 80%; } } }
    .footer {
      top: 0; } } }

@media ( min-width: 1024px ) {
  .container {
    .content {
      .background-img {
        height: 80vh;
        img {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          min-width: 100%;
          min-height: 100%; } } } } }
